.main__header {
	width: 100vw;
	height: calc(100vh - 3rem);
	display: grid;
	place-items: center;
	margin-top: 3rem;
}

.main__header-container {
	height: 100%;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	align-items: center;
	justify-content: space-between;
	gap: 6rem;
}

.main__header-left {
	margin-top: -3rem;
}

.main__header-left h4 {
	margin-bottom: 1rem;
	color: var(--color-secondary);
	font-weight: 400;
}

.main__header-left p {
	margin: 1rem 0 3.5rem;
	font-size: 1.1rem;
}

.main__header-right {
	display: grid;
	place-items: center;
	position: relative;
}

.main__header-circle {
	width: 25rem;
	height: 25rem;
	background: linear-gradient(75deg, var(--color-secondary), transparent);
	border-radius: 50%;
	position: absolute;
}

.main__header-image {
	position: relative;
}

.main__header-right:hover .main__header-circle {
	filter: blur(15px);
}

/* ==================== PROGRAMS =================== */
.programs {
	margin-top: 4rem;
}

.programs__wrapper {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 3rem;
	margin-top: 4rem;
}

.programs__program a {
	display: flex;
	margin-top: 2.5rem;
	align-items: center;
	justify-items: center;
	gap: 0.2rem;
}

.programs__program:hover a {
	background-color: var(--color-secondary);
	color: var(--color-gray-600);
}

/* ==================== VALUES =================== */
.values__container {
	display: grid;
	grid-template-columns: 38% 50%;
	gap: 12%;
}

.values__image {
	filter: saturate(25%);
	transition: var(--transition);
}

.values__image:hover {
	filter: saturate(100%);
}

.values__right p {
	margin: 1.5rem 0 3.5rem;
}

.values__wrapper {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 3.4rem 2.4rem;
}

.card.values__value {
	position: relative;
	padding-top: 3rem;
	text-align: left;
}

.card.values__value span {
	position: absolute;
	top: -1.5rem;
}

/* ==================== FAQS =================== */
.faq__wrapper {
	margin-top: 4rem;
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 1.5rem 5rem;
	align-items: flex-start;
}

.faq {
	background: var(--color-gray-500);
	padding: 2rem;
	border-radius: 1rem;
	cursor: pointer;
}

.faq div {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.faq__icon {
	background: transparent;
	color: var(--color-gray-100);
	border: none;
	font-size: 1.5rem;
}

.faq p {
	margin-top: 1.5rem;
}

/* ==================== TESTIMONIALS =================== */
.testimonials {
	width: 50rem;
	margin-inline: auto;
}

.testimonials__head {
	justify-content: center;
}

.testimonial {
	margin-top: 4rem;
	position: relative;
	text-align: left;
}

.testimonial__avatar {
	position: absolute;
	top: -2.5rem;
	left: calc(50% - 2.5rem);
	width: 5rem;
	height: 5rem;
	overflow: hidden;
	border: 2px solid transparent;
	border-radius: 1.5rem;
	transition: var(--transition);
	box-shadow: 0 1.5rem 2rem rgba(26, 26, 54, 0.4);
}

.testimonial:hover .testimonial__avatar {
	border-radius: 50%;
	border-color: var(--color-gray-400);
}

.testimonial__quote {
	font-style: italic;
	margin: 1rem 0 2rem;
}

.testimonial__title {
	margin-top: 0.3rem;
}

.testimonials__btn-container {
	width: fit-content;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 1rem;
	margin: 2.5rem auto 0;
}

.testimonials__btn {
	background: transparent;
	border: none;
	cursor: pointer;
}

.testimonials__btn svg {
	fill: var(--color-primary);
	font-size: 2rem;
	width: 3rem;
	height: 3rem;
}

/* ==================== FOOTER =================== */
footer {
	margin-top: 7rem;
	padding-top: 7rem;
	background: var(--color-primary);
	color: var(--color-gray-100);
	font-size: 0.9rem;
}

.footer__container {
	display: grid;
	grid-template-columns: 26rem 1fr 1fr 1fr;
	gap: 3rem;
}

.footer__container article {
	display: flex;
	flex-direction: column;
	gap: 0.7rem;
}

.footer__container article p {
	margin-top: 0.5rem;
}

.footer__container h4 {
	margin-bottom: 0.6rem;
	font-size: 1rem;
}

.footer__socials {
	display: flex;
	gap: 1rem;
	align-items: center;
	margin-top: 1.5rem;
}

.footer__socials a {
	background: var(--color-gray-100);
	padding: 0.7rem;
	border: 2px solid transparent;
	border-radius: 0.6rem;
	transition: var(--transition);
}

.footer__socials a svg {
	color: var(--color-gray-600);
}

.footer__socials a:hover {
	background-color: transparent;
	border-color: var(--color-gray-100);
}

.footer__copyright {
	color: var(--color-gray-100);
	border-top: 2px solid var(--color-primary-variant);
	text-align: center;
	padding: 2rem 0;
	margin-top: 5rem;
}

/* ================ MEDIA QUERY (media screens) ============ */

@media screen and (max-width: 1024px) {
	/* MIAN HEADER */
	.main__header {
		height: fit-content;
		padding: 10rem 0;
	}

	.main__header-container {
		gap: 0;
	}

	.main__header-circle {
		width: 16rem;
		height: 16rem;
	}

	/* PROGRAMS */

	.programs__wrapper {
		grid-template-columns: repeat(2, 1fr);
	}

	/* VALUES */
	.values__container {
		grid-template-columns: 1fr;
		gap: 4rem;
		justify-content: center;
	}

	.values__image {
		display: none;
	}

	.values__wrapper {
		gap: 4rem 3rem;
	}

	/* FAQs */
	.faq__wrapper {
		grid-template-columns: 1fr;
	}

	/* TESTIMONIALS */
	.testimonials {
		width: 75%;
	}

	/* FOOTER */
	.footer__container {
		grid-template-columns: repeat(2, 1fr);
		gap: 4rem;
	}
}

/* =========MEDIA QUERIES (small screens) ========== */

@media screen and (max-width: 600px) {
	/* Main header */
	.main__header {
		margin-top: 0;
		padding: 10rem 0 0;
		height: 100vh;
	}

	.main__header-container {
		grid-template-columns: 1fr;
	}

	.main__header-right {
		display: none;
	}

	/* programs */
	.programs__wrapper {
		grid-template-columns: 1fr;
		margin-top: 2rem;
	}

	.programs__program {
		width: 80%;
		margin-inline: auto;
	}

	/* values */
	.values__wrapper {
		grid-template-columns: 1fr;
		gap: 3rem;
	}

	.card.values__value {
		width: 80%;
		margin-inline: auto;
	}

	/* faqs */
	.faq__wrapper {
		margin-top: 2rem;
	}

	/* testimonials */
	.testimonials {
		width: 100%;
	}

	.testimonial__avatar {
		width: 4rem;
		height: 4rem;
		top: -2rem;
	}

	/* footer */
	.footer__container {
		grid-template-columns: 1fr;
	}

	.footer__container article {
		align-items: center;
	}

	.footer__container article p {
		text-align: center;
	}
}
