.trainers__container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 3rem;
}

.card.trainers {
	padding: 1rem;
}

.trainers__img {
	border-radius: 0 2rem;
	overflow: hidden;
}

.trainers h3 {
	margin-top: 2rem;
}

.trainers p {
	margin-top: 0.5rem;
}

.trainer__socials {
	display: flex;
	gap: 1rem;
	justify-content: center;
	margin: 2rem 0;
}

.trainer__socials a {
	font-size: 0.7rem;
	background: var(--color-gray-600);
	padding: 0.8rem;
	border-radius: 0.7rem;
	/* transition: var(--transition); */
}

.card.trainers:hover .trainer__socials a {
	background: var(--color-gray-500);
}

@media screen and (max-width: 1024px) {
	.trainers__container {
		grid-template-columns: repeat(2, 1fr);
		gap: 2.5rem;
	}
}

@media screen and (max-width: 600px) {
	.trainers__container {
		grid-template-columns: 1fr;
		gap: 2rem;
	}

	.card.trainer {
		margin-inline: auto;
		width: 90%;
	}
}
